* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,*:before,*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,:active {
  outline:none;
}

a:focus,a:active {
  outline:none;
}

nav,footer,header,aside{
  display: block
}

html,body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background-color: inherit;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited, a:hover{
  text-decoration: none;
}

ul,li {
  list-style: none;
}
img {
  vertical-align: top;
}

body{
  font-family: 'Roboto', 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  background-color: #15202b;
  color: #8899a6;
  min-height: 100vh; 
}

.container {
  margin: 0 auto;
  max-width: 1140px;
  position: relative;
  padding: 40px;
}

.banner-wrap{
  margin: 0 auto;
  width: 80%;
}

.banner{
  width: 100%;    
}

.heading-primary,
.heading-secondary{
  text-transform: uppercase;
  font-size: 42px;
  text-align: center;
  width: 700;
}

.heading-primary__top,
.heading-primary__bottom{
  display: block;
}

.heading-primary__bottom{
  font-size: 36px;
}

